.page-header {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px 12px 20px;
  }

  &__logo {
    &--tablet {
      display: none;
    }

    &--desktop {
      display: none;
    }

    &--text {
      margin: 5px 10px 0 0;
    }

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }
  }

  &__burger {
    display: block;
    cursor: pointer;
    border: none;
    width: 24px;
    height: 24px;
    background-color: @basic_white;
  }

  &__burger::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 18px;
    right: 20px;
    top: 23px;
    background-image: url("../img/icon/burger_open.svg");
    background-position: center;
  }

  &__burger--closed::before {
    content: "";
    background-color: @basic_white;
    position: absolute;
    width: 20px;
    height: 20px;
    right: 22px;
    top: 21px;
    background-image: url("../img/icon/burger_close.svg");
    background-position: center;
  }

  @media (max-width: @mobile-width-only) {
    &__burger--nojs {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 174px 1fr;
    column-gap: 95px;
    justify-content: space-between;
    align-items: center;
    padding: 28px 30px 0 30px;

    &__top {
      padding: 0;
    }

    &__burger {
      display: none;
    }

    &__logo {
      &--mobile {
        display: none;
      }

      &--tablet {
        display: block;
      }

      &--text {
        margin: 5px 10px 0 10px;
      }
    }
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 55px 110px 0 110px;
    box-sizing: border-box;
    column-gap: 264px;
    align-items: center;

    &__top {
      justify-content: start;
    }

    &__logo {
      &--tablet {
        display: none;
      }

      &--desktop {
        display: block;
      }

      &--text {
        width: 118px;
        height: 21px;
        margin-left: 13px;
      }
    }

    &--index {
      position: absolute;
      width: 100%;
    }
  }
}
